module.exports = "<div id=donate_page> <div class=donate_banner> <h1 class=mt-4> Support a supply chain revolution</h1> <p class=col-lg-12>Help Yellow Seed level the playing field by connecting global farmers to market opportunities based on trust and transparency.</p> <div class=\"donate-section col-xs-12 col-sm-12\"> <div class=photo-credit>Photo by Neil Palmer (CIAT)</div> <div class=row> <p class=col-lg-12>It's a great day to donate! Your donation provides more farmers visibility and access to ethical markets for their products.</p> </div> <div class=\"row top5 text-center\"> <form target=_blank id=donate-form action=https://www.paypal.com/cgi-bin/webscr accept-charset=UTF-8 method=post><input name=utf8 type=hidden value=✓><input type=hidden name=authenticity_token value=\"sv/4OnRavXhKU7hWceRtgxr5JOEvbiv0uTqZgny1OZnYotM8jqPCaqGMt32om9kReN2NIu8ecVcH2WH1Pd7bhg==\"> <input type=hidden name=cmd id=cmd value=_s-xclick> <input type=hidden name=hosted_button_id id=hosted_button_id value=Q8PFCNJK3PJ9W> <input type=submit name=commit value=Donate class=\"text-center donate-button btn\"> </form> </div> </div> </div> <div class=\"container-fluid yellowBg\"> <div class=container> <div class=\"row mt-4 bottom50\"> <div class=\"col-sm-4 donation-level\"> <div class=img1> </div> <div class=donate-white-box> <div class=dollar-amount>$100</div> <div class=dollar-description>Makes one new farm visible to ethical buyers.</div> </div> </div> <div class=\"col-sm-4 donation-level\"> <div class=img2> </div> <div class=donate-white-box> <div class=dollar-amount>$1,000</div> <div class=dollar-description>Helps translate the Yellow Seed website into multiple languages, giving more farmers access.</div> </div> </div> <div class=\"col-sm-4 donation-level\"> <div class=img3> </div> <div class=donate-white-box> <div class=dollar-amount>$10,000</div> <div class=dollar-description>Builds the partnerships and technology needed for traceability and verification of collective social impact.</div> </div> </div> </div> </div> </div> <div class=\"container donate-budget\"> <div class=\"row mt-4 bottom50\"> <div class=\"col-sm-5 col-lg-4 col-lg-offset-1\"> <img width=100% src=" + __webpack_require__(50) + " class=budget-graph> </div> <div class=\"col-sm-7 col-lg-6\"> <h2 class=top10>Your donation supports:</h2> <ul class=top20> <li><span>Giving more farmers a voice and access to better trade options, along with a better understanding of their value in the market and fair prices. </span></li> <li><span>Showcasing the stories and impact of progressive farmers and industry leaders working together to move beyond the level of compliance.</span></li> <li><span>Building a conscious trade network based on wholeness, where everyone that is a part of the cacao supply chain can add products or services of value and contribute to making it better.</span></li> </ul> <p class=top20>And this is just the beginning! We plan to add more features and scale to other products like coffee, tea, spices and dried fruit.</p> <p class=top20>In 2015, 43% of our yearly budget was spent on operations, 33% on web design &amp; coding, 12% on marketing, fundraising &amp; outreach, and 12% strategy &amp; design. Our 2016 operating budget is based on ongoing fundraising and is similarly allocated.</p> </div> </div> </div> <div class=\"container-fluid yellowBg\"> <div class=container> <div class=\"row donate-partnerships\"> <div class=\"col-md-12 col-lg-9 col-lg-offset-1 mt-4 bottom50\"> <h2>Funding partner &amp; sponsorship inquiries</h2> <p class=top20>Calling all revolutionaries! We invite you to seed the change. Yellow Seed is a 501(c)(3) project of Social and Environmental Entrepreneurs (SEE), a registered public charity. We rely on grants and corporate sponsorship to sustain our work and finance our technology development. </p> <p>If you are interested in supporting our work and helping us positively impact the lives of more farmers with a larger tax-deductible donation, please <a href=/contact>contact us</a>. We would be excited to connect!</p> <p>Any donation over $10,000 will be featured on our sponsors page, acknowledged in our newsletters (with supportive social media highlights) and in our future annual reports.</p> <p>You may also send checks directly to:</p> <ul> <li>Yellow Seed</li> <li>c/o Social and Environmental Entrepreneurs (SEE)</li> <li>23532 Calabasas Road Suite A</li> <li>Calabasas, CA 91302</li> </ul> <p>The EIN number for Social and Environmental Entrepreneurs (SEE) is 95-4116679.</p> </div> </div> </div> </div> <div class=container-fluid> <div class=container> <div class=\"row donate-participate\"> <div class=\"col-md-5 col-lg-6 col-lg-offset-1 mt-4\"> <h2>How else can I contribute?</h2> <p class=top20>While Yellow Seed values your financial contribution, community voices, participation and support are what drives the design of our platform. Want to learn more? Check out our <a href=/faq#contribute>FAQ</a>.</p> </div> <div class=\"col-md-7 col-lg-5 cacao-beans-container\"> <img src=/assets/cacao-beans-pods.png class=\"cacao-beans-pods img-responsive center-block\"> </div> </div> </div> </div> <span id=sbmarwusasv5></span> <div style=\"visibility:hidden;position:absolute;width:100%;top:-10000px;left:0;right:0;transition:visibility 0s linear .3s,opacity .3s linear;opacity:0\"> <div style=width:100%;height:100%;position:fixed;top:0;left:0;z-index:2000000000;background-color:#fff;opacity:.5></div> <div style=\"margin:0 auto;top:0;left:0;right:0;position:absolute;border:1px solid #ccc;z-index:2000000000;background-color:#fff;overflow:hidden\"><iframe src=\"https://www.google.com/recaptcha/api2/bframe?hl=en&amp;v=r20170925162623&amp;k=6Le1NRsTAAAAALcfoCGML8KhFXu-qJJr9am3pPS8#ojmm4yl7pmrp\" title=\"recaptcha challenge\" frameborder=0 scrolling=no sandbox=\"allow-forms allow-popups allow-same-origin allow-scripts allow-top-navigation allow-modals allow-popups-to-escape-sandbox\" name=ojmm4yl7pmrp style=width:100%;height:100%></iframe></div> </div></div>";

