module.exports = "<div id=homepage> <div class=homepage-banner> <h1>Join the Conscious Trade movement</h1> <div class=\"col-xs-12 col-md-10\"> <p class=center>Yellow Seed is a Conscious Trade Network working to make markets more transparent, accessible, and participatory. We believe our global food trade cand be designed to promote greater farmer equity, ecological conservation, and community resilience.</p> <div class=top30> <a class=btn-default target=_blank href=/donate>donate now</a> </div> </div> <div class=oxc-link> <p>Looking to sell or buy Conscious Cacao? Check out Yellow Seed’s marketplace partner:</p> <img><a href=http://www.originxchange.co target=_blank></a> </div> </div> <div class=container-fluid> <div class=container> <div class=row> <div class=\"col-lg-12 top60\"> <h2>What we do</h2> <div class=row> <div class=col-lg-12> <p class=top20>We are building a global Conscious Trade movement, where markets value farmers and their care of the land, and buyers know they can make a positive impact. Our work will:</p> </div> </div> <div class=\"row top30\"> <div class=col-md-3> <img class=impact-icon src=" + __webpack_require__(4) + " alt=\"Unique varietals icons\"> <h4>unique varietals cultivated by the origins listed on origin x change</h4> </div> <div class=col-md-3> <img class=impact-icon src=" + __webpack_require__(2) + " alt=\"Warehouse boxes icon\"> <h4>unique varietals cultivated by the origins listed on origin x change</h4> </div> <div class=col-md-3> <img class=impact-icon src=" + __webpack_require__(5) + " alt=\"Forest conservation icon\"> <h4>unique varietals cultivated by the origins listed on origin x change</h4> </div> <div class=col-md-3> <img class=impact-icon src=" + __webpack_require__(3) + " alt=\"Food security icon \"> <h4>unique varietals cultivated by the origins listed on origin x change</h4> </div> </div> </div> </div> <div class=\"row top80\"></div> </div> </div> <div class=\"container-fluid quote-container\"> <div class=row> <div class=col-lg-10> <p>Conscious Trade means producers, distributors and consumers are empowered to shape and improve the system together, in a way that levels the playing field for farmers, preserves rainforests, and strengthens local farming communities.</p> </div> </div> </div> <div class=\"container-fluid bg-yellow oxc-website\"> <div class=container> <div class=row> <div class=\"col-lg-12 top60\"> <img> <h2>Introducing Origin X Change: Yellow Seed’s marketplace partner </h2> <div class=row> <div class=col-lg-12> <p class=top20>Origin X Change is the Conscious Cacao Marketplace built by Yellow Seed. It is a community-driven marketplace for cacao growers, service intermediaries and buyers to connect and trade with one another. Deeply rooted in the exchange of trust, Origin X Change provides a high level of transparency for buyers, and the ability to find products that contribute to the impact they care about.</p> <p>Yellow Seed has partnered with Origin X Change to operate the marketplace, so it can focus on expanding the global Conscious Trade movement.</p> </div> </div> </div> </div> <div class=\"row top80\"></div> </div> </div> <div class=container-fluid> <div class=container> <div class=row> <div class=\"col-lg-12 top60\"> <h2>We track the collective impact of Conscious Trade</h2> <div class=row> <div class=col-lg-12> <p class=top20>One of our core commitments is highlighting the impact of Conscious Trade on farmers, their communities and the environment. As the Conscious Trade network grows, Yellow Seed's visual and analytic tools aim to paint a portrait of the whole network, pointing to ways we can work better together.</p> </div> </div> <div class=\"row top30\"> <div class=col-md-3> <img class=impact-icon src=" + __webpack_require__(4) + " alt=\"Unique varietals icons\"> <h3>68</h3> <h5>unique varietals cultivated by the origins listed on origin x change</h5> </div> <div class=col-md-3> <img class=impact-icon src=" + __webpack_require__(2) + " alt=\"Warehouse boxes icon\"> <h3>68</h3> <h5>metric tons of cacao produced on average annually</h5> </div> <div class=col-md-3> <img class=impact-icon src=" + __webpack_require__(5) + " alt=\"Forest conservation icon\"> <h3>68</h3> <h5>ha. of forest protected by the origins of Yellow seed</h5> </div> <div class=col-md-3> <img class=impact-icon src=" + __webpack_require__(3) + " alt=\"Food security icon \"> <h3>68</h3> <h5>hectares of other crops provides food security at origin</h5> </div> </div> </div> </div> <div class=\"row top80\"></div> </div> </div> <div class=\"container-fluid bg-yellow\"> <div class=container> <div class=row> <div class=\"col-lg-12 top60\"> <iframe src=https://player.vimeo.com/video/229867830 width=640 height=360 frameborder=0 webkitallowfullscreen mozallowfullscreen allowfullscreen></iframe> <p><a href=https://vimeo.com/229867830>Yellow Seed Pilot with Co-op ACCC - Farmer Voice, Buyer Choice</a> from <a href=https://vimeo.com/yellowseedorg>Yellow Seed</a> on <a href=https://vimeo.com>Vimeo</a>.</p> <h2>The Farmer Choice Fund is a powerful way to support farmer-directed projects</h2> <div class=row> <div class=col-lg-12> <p class=\"top20 paragraph\">The fund allows farmers to decide what impact is needed, helps them set goals and targets, and enables buyers to track how the prices they pay contribute to that impact. We are piloting the Farmer Choice Fund with Co-op ACCC in Peru, helping them raise $3000 to expand their unique agroforestry system, preserve biodiversity, ensure food security, and increase resilience to climate change.</p> <a class=\"btn-white-outline btn-default top30\" target=_blank href=/farmer>learn more about the pilot</a> </div> </div> </div> </div> <div class=\"row top80\"></div> </div> </div> <div class=container> <div class=row> <div class=\"col-xs-12 col-md-12\"> <h3 class=\"text-center yellow-heading top90 bottom50\">Our Partners.</h3> </div> </div> <div class=\"row top20 bottom50 center-vertically\"> <div class=\"col-xs-4 text-center\"> <img class=\"img-responsive center-block\" src=" + __webpack_require__(66) + " alt=\"Partner logo 01 \"> </div> <div class=\"col-xs-4 text-center\"> <img class=\"img-responsive center-block\" src=" + __webpack_require__(67) + " alt=\"Partner logo 02 \"> </div> <div class=\"col-xs-4 text-center\"> <img class=\"img-responsive center-block\" src=" + __webpack_require__(68) + " alt=\"Partner logo 03 \"> </div> </div> </div> </div> ";

